import React, { useState, useEffect } from 'react';
import MenuItem from './menuItems';
import "./menumobile.css";
  
const Menu = (props) => {

        const [menuItemList, setMenuItemList] = useState([]);
        const [isMenuMobile, setMenuMobile] = useState(true)

        const toggleMenu = (isMenuMobile) => {
            if (window.innerWidth <= 993) {
                setMenuMobile(!isMenuMobile)
                animationFunction("menuWrapper", "margin-left", 0, "-68vw", isMenuMobile)
                animationFunction("menuBar1", "opacity", 0, 1, isMenuMobile)
                animationFunction("menuBar3", "opacity", 0, 1, isMenuMobile)
                animationFunction("menuBar2", "width", "20%", "50%", isMenuMobile)
                let toggleMenu = document.querySelectorAll(".toggleMenu")
                toggleMenu[0].className = isMenuMobile ? "toggleMenu isToggled" : "toggleMenu";
            }
        }

        const animationFunction = (id, styleItem, option1, option2, isMenuMobile) => {
            let targetedItem = document.getElementById(id)
            targetedItem.style[styleItem] = isMenuMobile ? option1 : option2
        }
    
        function fixMenuForComputerLayout() {
            let menu = document.getElementById("menuWrapper")
            if (window.innerWidth >= 993) {
                menu.style.marginLeft = "0px"
            } else if (window.innerWidth <= 992 && isMenuMobile){
                menu.style.marginLeft = "-340px"
            }
        }
          
        window.onresize = fixMenuForComputerLayout;

        useEffect(() => {
        let i= 0;
        const list = []
        for (const [entry, value] of Object.entries(props.data)) {
            i++
            list.push(
                <MenuItem
                    key = {i}
                    entry = {entry}
                    id = {entry}
                    data = {props.data[entry]}
                    value = {value}
                    click = {props.upDateData}
                    click2 = {toggleMenu}
                >{value.title}
                </MenuItem>
            )
            setMenuItemList(list)
        }
    }, [props.data])

    console.log("menu")

    return (
        <>
            <div 
                className="toggleMenu"
                onClick={() => {toggleMenu(isMenuMobile)}}                    
            >
                <div className = "menuBar" id = "menuBar1"></div>
                <div className = "menuBar" id = "menuBar2"></div>
                <div className = "menuBar" id = "menuBar3"></div>
            </div>
            <div id = "menuWrapper">
                <div id = "menu">
                    <table>
                        <thead 
                        >
                            <tr>
                                {menuItemList}
                            </tr>
                        </thead>
                    </table>
                </div>
            </div>
        </>
    );
}
  
export default Menu;
