import React, { useState } from 'react';
import "./account.css";
import Title from './title';
import AccountCreation from './accountCreation';
import AccountConnexion from './accountConnexion';

const Account = (props) => {

    console.log("account")

    const [hasAccount, setHasAccount] = useState(null)

    const handleHasAccount = (hasAccount) => {
        setHasAccount(hasAccount)
    }
    
    if(!props.online) {
        return (
            <div id="displayWrapper">
                <div id="displayAccount">
                    <Title
                        css = "displayTitle"
                    >IDENTIFICATION</Title>
                    {hasAccount === null && 
                        <>
                            <h3>Se connecter</h3>
                            <div className='accountButton' onClick={() => handleHasAccount(true)}>Vous avez déjà un compte</div>
                            <div className='accountButton' onClick={() => handleHasAccount(false)}>Créer un compte</div>
                        </>
                    }
                    {hasAccount !== null && !hasAccount ? <AccountCreation></AccountCreation> : <AccountConnexion></AccountConnexion>}
                </div>
            </div>
        )
    } else {
        return (
            <div id="displayWrapper">
                <div id="displayAccount">
                    <Title
                        css = "displayTitle"
                    >IDENTIFICATION</Title>
                    <h3>Bonjour {props.online.name}, {props.online.gender === 1 ? "vous êtes bien connecté : )" : "vous êtes bien connectée : )"}</h3>
                    <img src={`${`${process.env.PUBLIC_URL}/Accessories/A1073.jpg`}`}></img><br></br>
                    <div className='accountButton' onClick={() => props.handleAccountOnline(false)}>Se déconnecter</div>
                </div>
            </div>
        )
    }
}

export default Account;