import React, { useState } from 'react';
import "./basketline.css"
  
const BasketLine = (props) => {

    const fiftyGrams = 20;
    const [qty, setQty] = useState(props.qty)
    const [bagSize, setBagSize] = useState(props.bagSize)
    const line = {
        name : props.data.name,
        ref : props.data.ref,
        price : props.data.price,
        qty : qty,
        bagSize : bagSize
    }

    const handleBagSize = (bagSize) => {
        setBagSize(bagSize)
        setQty(1) /*handleQuantity(1) is interesting also, to change packaging size dynamically*/
    }

    const handleQuantity = (value) => {
        setQty(previousQty => previousQty + value)
    }

    const handleValidate = () => {
        handleCloseOrDeleteBasketLine()
        props.handleBasket(line, qty)
        props.handleShowConfirmation(line)
    }

    const handleCloseOrDeleteBasketLine = () => {
        props.handleShowBasket(false)
    }

    return (
        <div>
            <table className="basketLine">
                <tbody>
                    <tr>
                        <th className="basketlineLeft">{props.data.ref} - {props.data.name}<br></br>
                        <div className="basketLineInfo basketLineBagSize">{bagSize * 100} g</div>
                        </th>
                        <th className="basketlineCenter1">
                            <div 
                                className="basketLineButton qtySetter" 
                                onClick={() => qty > 0 && handleQuantity(-1)}
                            >-</div>
                            <div 
                                className="basketLineInfo" 
                                style = {{width : "30%"}}
                            >{qty}</div>
                            <div 
                                className="basketLineButton qtySetter" 
                                onClick={() => handleQuantity(1)}
                            >+</div>
                        </th>
                        <th className="basketlineCenter2">
                            <div 
                                className="basketLineInfo basketLinePrice"
                            >{props.data.price * qty * bagSize} €</div>
                        </th>
                        <th className="basketlineRight">
                                <div 
                                    className="basketLineButtonAdd" 
                                    id = "basketLineAddButton" 
                                    onClick = {() => qty > 0 && handleValidate()}
                                >Ajouter</div>
                                <div 
                                    className="closeOrDelete" 
                                    onClick = {() => handleCloseOrDeleteBasketLine()}
                                >&#10006;</div>
                        </th>
                    </tr>
                </tbody>
            </table>
            <table className="basketlineUnder">
                <tbody>
                    <tr>
                        <th>
                            {props.data.price <= fiftyGrams ?
                                <div 
                                    className = "bagSizeSetter basketLineButton"
                                > 100 g </div> :
                                <>
                                    <div 
                                        className = "bagSizeSetter basketLineButton"
                                        style = {{background : bagSize === 0.5 ? "rgb(84, 182, 84)" : "black"}}
                                        onClick={() => handleBagSize(0.5)}
                                    > 50 g </div>
                                    <div 
                                        className = "bagSizeSetter basketLineButton"
                                        style = {{background : bagSize === 1 ? "rgb(84, 182, 84)" : "black"}}
                                        onClick={() => handleBagSize(1)}
                                    > 100 g </div>
                                </>
                            }
                            <div 
                                    className="basketLineButtonAdd" 
                                    id = "basketLineMobileAddButton" 
                                    onClick = {() => qty > 0 && handleValidate()}
                            >Ajouter</div>
                        </th>
                    </tr>
                </tbody>
            </table>
        </div>
    );
}

  
export default BasketLine;