import React from "react";
import { useHistory, useParams } from 'react-router-dom';
import Animation from "./animation";
import HistoryManage from "./historymanage";
import "./thumbnailproduct.css"

const ThumbNailProduct = (props) => {

    const history = useHistory()
    const {menuItem, category} = useParams()

    Animation(`productContainer${props.index}`, "productContainer", props.index)

    let origin;
    let teaColor;

    for (const [entry, value] of Object.entries(props.data)) {
        switch (props.data.origin) {
            case 1.1:
                origin = "Darjeeling";
                break;
            case 2:
                origin = "Chine";
                break;
            case 2.5:
                origin = "Formose";
                break;
            case 1.2:
                origin = "Assam";
                break;
            case 3:
                origin = "Ceylan";
                break;
            case 4:
                origin = "Japon";
                break;
          default:
                origin = "";
        }
        switch (props.data.teaColor) {
            case 1:
                teaColor = "Thé Noir";
                break;
            case 2:
                teaColor = "Thé Vert";
                break;
            case 3:
                teaColor = "Thé Blanc";
                break;
            case 4:
                teaColor = "Thé Bleu";
                break;
            case 5:
                teaColor = "Rooibos";
                break;
            case 6:
                teaColor = "Infusion";
                break;
            case 7:
                teaColor = "Thé mûr";
                break;
          default:
                teaColor = "";
        }
    }

    const stateToSave = {
        origin,
        teaColor,
        jardinPremier: props.data.jardinPremier,
        region: props.data.region,
        looseTea: props.data.looseTea,
        paragraph: props.data.paragraph,
        name: props.data.name,
        price: props.data.price,
        ref: props.data.ref,
        technics: props.data.technics
    }

    const handleData = () => {
        HistoryManage(props.data, props.entry, `menu/${menuItem}/${category}`, stateToSave, history)
    }

    const handleShowBasket = () => {
        props.handleShowBasket({
            name : props.data.name,
            price : props.data.price,
            ref : props.data.ref
        })
    }

    console.log("Thumbnail Product")

    return(
        <>
        <div 
            className = "productContainer" 
            id = {`productContainer${props.index}`}
        >
            <div
                className = {"product"}
                entry = {props.entry}
                onClick = {() => handleData(props.data, props.entry)}
            >
                <img 
                    className = "thumbNailProduct"
                    src = {`${`${process.env.PUBLIC_URL}/${props.pic}`}.jpg`} alt = {props.pic}
                ></img>
                <div className="thumbnailProductInfo">
                    {props.data.name}<br></br>
                    {origin}<br></br>
                    {teaColor}<br></br>
                    {props.data.ref} - {props.data.price} € /100g
                </div>
            </div>
            <div className="buyButtonContainer">
                <div 
                    className = "buyButton"
                    onClick={() => handleShowBasket()}
                >
                    ACHETER
                </div>
            </div>
        </div>
        </>
    )
};

export default ThumbNailProduct;