import React from "react";
import Title from "./title";
import { useHistory, useParams } from 'react-router-dom';
import Animation from "./animation";
import HistoryManage from "./historymanage";

const ThumbNail = (props) => {

    const history = useHistory()
    const categoryName = history.location.state.categoryName
    const {menuItem} = useParams()

    Animation(props.classN + props.index, props.classN, props.index)

    const stateToSave = {
        value : props.data,
        categoryName : categoryName
    }

    const handleData = () => {
        HistoryManage(props.data, props.entry, `menu/${menuItem}`, stateToSave, history)
    }

    console.log("Thumbnail")

    return(
        <>
            <div
                className = {props.classN}
                entry = {props.entry}
                onClick = {() => handleData()}
                id = {props.classN + props.index}
            >
                <Title
                    css = {props.css}
                >
                    {props.data.title}
                </Title>
                <img 
                    style = {{width: "100%", height: "100%", marginBottom: "-4px"}}
                    src = {`${`${process.env.PUBLIC_URL}/${props.pic}`}.jpg`} alt = {props.pic}
                ></img>
            </div>
        </>
    )
};

export default ThumbNail;

