import React, { useState } from 'react';
import "./basketline.css"

const OrderBlock = (props) => {

    const [qty, setQty] = useState(props.qty)
    const [bagSize, setBagSize] = useState(props.bagSize)
    const fiftyGrams = 20;

    const line = {
        name : props.data.name,
        ref : props.data.ref,
        price : props.data.price,
        qty : qty,
        bagSize : bagSize
    }

    const handleQuantity = (value) => {
        setQty(previousQty => previousQty + value)
    }

    const handleBagSize = (bagSize) => {
        setBagSize(bagSize)
        setQty(1) /*handleQuantity(1) is interesting also, to change packaging size dynamically*/
    }

    const handleValidate = () => {
        props.handleBasket(line, qty)
        props.handleAddedToBasket(line)
        setQty(0)
    }

    return (
        <div id="order">
            <div id="selectQty">
                <table style = {{width : "100%"}}>
                    <tbody>
                        <tr>
                            <th 
                                style = {{paddingTop: "5px", paddingBottom : "5px"}}
                            >Ref : {props.data.ref}</th>
                        </tr>
                    </tbody>
                </table>
                <table style = {{width : "100%"}}>
                    <tbody>
                        <tr>
                            <th 
                                className = "displayProductButton" 
                                style = {{paddingTop: "5px" , paddingBottom : "5px", width: "33%"}} 
                                onClick={() => qty > 0 && handleQuantity(-1)}
                            >-</th>
                            <th 
                                style = {{paddingTop: "5px" , paddingBottom : "5px", width: "33%"}}
                            >{qty}</th>
                            <th 
                                className = "displayProductButton" 
                                style = {{paddingTop: "5px" , paddingBottom : "5px", width: "33%"}} 
                                onClick={() => handleQuantity(1)}
                            >+</th>
                        </tr>
                    </tbody>
                </table>
                <table style = {{width : "100%"}}>
                    <tbody>
                        <tr>
                            {props.data.price <= fiftyGrams ?
                                    <th>
                                        <div 
                                            className = "displayProductButton" 
                                            style = {{paddingTop: "5px" , paddingBottom : "5px", width: "35%", display: "inline-block", float: "left"}}
                                        >100 g</div>
                                    </th> :
                                <>
                                    <th>
                                        <div 
                                            className = "displayProductButton" 
                                            style = {{paddingTop: "5px" , paddingBottom : "5px", width: "35%", display: "inline-block", float: "left", background : bagSize == 0.5 ? "rgb(84, 182, 84)" : "black"}} 
                                            onClick={() => handleBagSize(0.5)}
                                        >50 g</div>
                                        <div 
                                            className = "displayProductButton" 
                                            style = {{paddingTop: "5px" , paddingBottom : "5px", width: "35%", display: "inline-block", float: "left", background : bagSize == 1 ? "rgb(84, 182, 84)" : "black"}} 
                                            onClick={() => handleBagSize(1)}
                                        >100 g</div>
                                    </th>
                                </>
                            }
                        </tr>
                    </tbody>
                </table>
            </div>
            <div id = "addToCart">
            <table style = {{width : "100%"}}>
                    <tbody>
                        <tr>
                            <th 
                                style = {{paddingTop: "5px", paddingBottom : "5px"}}
                            >{props.data.price * bagSize} € - {bagSize * 100} g</th>
                        </tr>
                    </tbody>
                </table>
                <table style = {{width : "100%"}}>
                    <tbody>
                        <tr>
                            <th 
                                className = "displayProductButton" 
                                style = {{paddingTop: "5px" , paddingBottom : "5px"}}
                                onClick = {() => qty > 0 && handleValidate()}
                            >Acheter</th>
                        </tr>
                    </tbody>
                </table>
                <table style = {{width : "100%"}}>
                    <tbody>
                        <tr>
                            <th 
                                style = {{paddingTop: "5px", paddingBottom : "5px"}}
                            >{props.data.price * qty * bagSize} €</th>
                        </tr>
                    </tbody>
                </table>
            </div>
            <br></br>
        </div>
    )
};

export default OrderBlock;