import React from 'react';
import { useHistory } from 'react-router-dom';
import HistoryManage from './historymanage';
  
const MenuItem = (props) => {

    const history = useHistory();

    const stateToSave = {
        value : props.value,
        categoryName : props.data.title
    }

    const handleData = (id) => {
        let chosen =  document.getElementById(id)
        if(chosen === "grandCru") chosen.style.background = "red"
        HistoryManage(props.value, props.entry, "menu", stateToSave, history)
        props.click2(false)
    }

    console.log("menuItem")

    return (
        <>       
            <th
                id = {props.id}
                onClick={() => handleData(props.id)}
                className = "menuItem"
            >
                {props.children}
            </th>
        </>
    );
}
  
export default MenuItem;