import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Switch, Route, useParams } from 'react-router-dom';
import "./main.css";
import HomePage from "./homepage";
import menuData from './menuData';
import Header from './header';
import Menu from './menu';
import DisplayCategory from './displaycategory';
import DisplayCategoryItems from './displaycategoryitems';
import DisplayProduct from './displayproducts'
import Footer from './footer';
import DisplayBasket from './displaybasket';
import Account from "./account"

const App = () => {

  const [data, setData] = useState(menuData)
  const [basket, setBasket] = useState([])
  const [constantElements, setConstantElements] = useState([])
  const [online, setOnline] = useState({
    gender: 1,
    surname: "Taillardat",
    name: "Francois"
  })

  // console.log("app")

  const handleBasket = (itemName, increment) => {
    if(basket.length === 0) {
      setBasket(previousBasket => [...previousBasket, itemName])
    } else {
      const exist = basket.find(basketLine => ((basketLine.ref === itemName.ref) && (basketLine.bagSize === itemName.bagSize)))
      if(exist) {
        setBasket(basket.map(basketLine => ((basketLine.ref === itemName.ref) && (basketLine.bagSize === itemName.bagSize)) ? {...exist, qty : exist.qty + increment} : basketLine))
      } else {
        setBasket(previousBasket => [...previousBasket, itemName])
      }
    }
  }

  const handleRemoveFromBasket = (itemName) => {
    setBasket(basket.filter(basketLine => !(itemName.ref === basketLine.ref && itemName.bagSize === basketLine.bagSize)))
  }

  const handleAccountOnline = (isOnline) => {
    setOnline(isOnline)
  }

  useEffect(() => {
    setData(menuData)
    setConstantElements(
      [
        <Header></Header>,
        <Menu
            data = {data.menu} 
        ></Menu>,
        <Footer></Footer>
      ])
  }, [data])

 return (
    <>
      <Router>
          {constantElements[0]}
          {constantElements[1]}
        <div>
          <Switch>
              <Route exact path = "/accueil" component = {HomePage}></Route>
              {/* <Route path = "/menu/(\*{0})" render = {() => <Display data={data}></Display>}></Route> */}
              <Route path = "/menu/:menuItem" exact render = {() => 
                <DisplayCategory
                ></DisplayCategory>}>
              </Route>
              <Route path = "/menu/:menuItem/:category" exact render = {() =>
                <DisplayCategoryItems
                  handleBasket = {handleBasket}
                ></DisplayCategoryItems>}>
              </Route>
              <Route path = "/menu/:menuItem/:category/:product" exact render = {() =>
                <DisplayProduct
                  basket = {basket}
                  handleBasket = {handleBasket}
                ></DisplayProduct>}>
              </Route>
              <Route path = "/basket" exact render = {() =>
                <DisplayBasket
                  basket = {basket}
                  handleBasket = {handleBasket}
                  handleRemoveFromBasket = {handleRemoveFromBasket}
                ></DisplayBasket>}>
              </Route>
              <Route path = "/account" exact render = {() =>
                <Account
                  online={online}
                  handleAccountOnline = {handleAccountOnline}
                ></Account>}>
              </Route>
              <Route exact path = "" component = {HomePage}></Route>
          </Switch>
        </div>
      </Router>
      {constantElements[2]}
    </>
  );
}

export default App;