import React from 'react';
import BasketLine from './basketline';
import "./displayproductbasket.css"
  
const DisplayProductBasket = (props) => {

    console.log("Display Product Basket")

    return (
        <div id="displayProductBasket">
            <BasketLine
                data = {props.data}
                qty = {0}
                bagSize = {1}
                handleBasket = {props.handleBasket}
                handleShowBasket = {props.handleShowBasket}
                handleShowConfirmation = {props.handleShowConfirmation}
            ></BasketLine>
        </div> 
    )
} 
  
export default DisplayProductBasket;