import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import loremIpsum from './loremipsum';
import Animation from './animation';
import "./displayproduct.css"
import OrderBlock from './orderblock';
import BasketLine from './basketline';

const DisplayProducts = (props) => {

    const [addedToBasket, setAddedToBasket] = useState(false)
    const location = useLocation()
    const info = location.state;

    Animation("productPicture", "productPicture", 1)
    
    const handleAddedToBasket = (addedToBasket) => {
        setAddedToBasket(addedToBasket)
        setTimeout(() => {
            setAddedToBasket(false)
        }, 3000);
    }

    console.log("Display Product")

        return (
            <>
            <div id = "displayWrapper">
                 <div id = "displayCategoryProducts">
                    <div id = "productPictureContainer">
                        <img
                            id = "productPicture"
                            className = "productPicture"
                            key = "mainPic"
                            src = {`${`${process.env.PUBLIC_URL}/T3-Images/${info.ref}`}.jpg`} alt = {info.ref}
                        ></img>
                    </div>
                    <div id="productInfo">
                        <div id="productName">
                            {info.name}
                            <br></br>
                        </div>
                        <div id="teaColor">
                            {info.teaColor}
                            <br></br>
                        </div>
                        <div id="productOriginAndRegion">
                            {info.region && info.region !== info.origin && [info.region, [<br></br>]]}
                            {info.origin && [info.origin, [<br></br>]]}
                        </div>
                        <div id="jardinPremier">
                            {info.jardinPremier && ["- Jardin Premier* -", [<br></br>]]}
                        </div>
                        <div id="mobileOrderBlock">
                            <OrderBlock
                                data = {info}
                                handleBasket = {props.handleBasket}
                                qty = {0}
                                bagSize = {1}
                                handleAddedToBasket = {handleAddedToBasket}
                            ></OrderBlock>
                        </div>
                        <div id="incipit">
                            <br></br>
                            {info.paragraph}
                            <br></br>
                            <br></br>
                            <img className = "lineAndSquare" src = {`${`${process.env.PUBLIC_URL}/lineAndSquare.png`}`} alt = "lineAndSquare"></img>
                            <br></br>
                            <br></br>
                        </div>
                        <p id="productDescription">{loremIpsum}</p><br></br>
                        <p id="productDescription">{loremIpsum}</p><br></br>
                        <p id="productDescription">{loremIpsum}</p><br></br>
                        <p id="productDescription">{loremIpsum}</p><br></br>
                        <p id="productDescription">{loremIpsum}</p><br></br>
                        <div id="jardinPremierContainer">
                            {info.jardinPremier &&
                            <>
                                <img id="blocJardinPremier" src = {`${`${process.env.PUBLIC_URL}/bloc_jardin_premier.gif`}`} alt = "bloc_jardin_premier"></img>
                                <br></br>
                                <br></br>
                            </>
                            }
                        </div>
                        <div id="preparation">
                            CONSEILS DE PREPARATION <br></br>
                            {info.technics[0]}g / 20cl - {info.technics[1]}°C - {info.technics[2]} min
                            <br></br>
                            <br></br>
                        </div>
                        <div id = "otherInfo">
                            <img className = "lineAndSquare" src = {`${`${process.env.PUBLIC_URL}/lineAndSquare.png`}`} alt = "lineAndSquare"></img>
                            <br></br>
                            <br></br>
                            <div>
                                <strong>Caractéristiques : </strong><br></br>
                                <strong>Conditionnement : </strong>
                            </div>
                            <br></br>
                        </div>
                        <div id="orderBlock">
                            <OrderBlock
                                data = {info}
                                handleBasket = {props.handleBasket}
                                qty = {0}
                                bagSize = {1}
                                handleAddedToBasket = {handleAddedToBasket}
                            ></OrderBlock>
                        </div>
                    </div>
                    <img id="lineAndLogo" src = {`${`${process.env.PUBLIC_URL}/lineAndLogo.jpg`}`} alt = "lineAndLogo"></img>
                    <div id = "seeAlso">
                        <br></br>
                        En construction
                    </div>              
                </div>
            </div>
            {addedToBasket && 
            <table style = {{fontWeight: "normal", background : "darkslategray", width: "100vw", bottom: "20vw", position : "fixed", color: "white", paddingTop: "20px", paddingBottom: "20px", margin: "auto", textAlign: "center"}}>
                <tbody>
                    <tr>
                        <th>{addedToBasket.ref} - {addedToBasket.name} - {addedToBasket.bagSize * 100} g <br></br> Quantité : {addedToBasket.qty} <br></br>a bien été ajouté à votre panier.<br></br><br></br>La Maison Mariage Frères vous remercie.</th>
                    </tr>
                </tbody>
            </table>
            }
            </>
        )
};

export default DisplayProducts;