import React, { useState } from 'react';
import "./basketline.css"
  
const BasketListItem = (props) => {

    const [qty, setQty] = useState(props.data.qty)

    const line = {
        name : props.data.name,
        ref : props.data.ref,
        price : props.data.price,
        qty : props.data.qty,
        bagSize : props.bagSize
    }

    const handleQuantity = (value) => {
        setQty(previousQty => previousQty + value)
        props.handleBasket(line, value)
    }

    const handleCloseOrDeleteBasketLine = (line) => {
        props.handleRemoveFromBasket(line)
    }

    return (
        <div>
            <table className="basketLine">
                <tbody>
                    <tr>
                        <th className="basketlineLeft">{line.ref} - {line.name}<br></br>
                        <div className="basketLineInfo basketLineBagSize">{line.bagSize * 100} g</div>
                        </th>
                        <th className="basketlineCenter1">
                            <div 
                                className="basketLineButton qtySetter" 
                                onClick={() => qty > 0 && handleQuantity(-1)}
                            >-</div>
                            <div 
                                className="basketLineInfo" 
                                style = {{width : "30%"}}
                                // à surveiller
                                 >{props.data.qty}</div>
                            <div 
                                className="basketLineButton qtySetter" 
                                onClick={() => handleQuantity(1)}
                            >+</div>
                        </th>
                        <th className="basketlineCenter2">
                            <div 
                                className="basketLineInfo basketLinePrice"
                            >{line.price * line.qty * line.bagSize} €</div>
                        </th>
                        <th className="basketlineRight">
                            <div 
                                className="closeOrDelete" 
                                onClick = {() => handleCloseOrDeleteBasketLine(line)}
                            >&#10006;</div>
                        </th>
                    </tr>
                    <tr>
                    </tr>
                </tbody>
            </table>
        </div>
    );
};
  
export default BasketListItem;


