import React, { useState } from "react";
import ThumbNailProduct from "./thumbnailproduct";
import { useEffect } from "react";
import { useLocation } from 'react-router-dom';

const List = (props) => {

const [list, setList] = useState()
const location = useLocation()
let i = 0;
const newList = []

useEffect(() =>{
    for (const [entry, value] of Object.entries(location.state.value.refs)) {
        i++
        // alert("render")
        newList.push(
            <ThumbNailProduct
                key = {entry}
                entry = {entry}
                data = {value}
                pic = {value.ref}
                index = {i}
                handleShowBasket = {props.handleShowBasket}
            ></ThumbNailProduct>
        )
    } setList(newList)
}, [props.data])

return (
    <div>{list}</div>
)

}

export default List;