import React from "react";
import ThumbNail from "./thumbnail";

const DisplayListe = (props) => {
    const css = "category";
    const cssTitle = "categoryTitle"

    const liste = [];
    let i = 0;
    for (const [entry, value] of Object.entries(props.data)) {
        i++
        liste.push(
            <ThumbNail
                classN = {css}
                key = {entry}
                entry = {entry}
                css = {cssTitle}
                data = {value}
                pic = {value.mainPic}
                index = {i}
            ></ThumbNail>
        )
    }

    console.log("Display Liste")

    return(
        <>
            <div>
                {liste}
            </div>
        </>
    )
};

export default DisplayListe