import React, { useMemo, useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import Title from './title';
import DisplayProductBasket from './displayproductbasket';
import List from './list';
  
const DisplayCategoryItems = (props) => {

    const location = useLocation()

    const [addedItem, setAddedItem] = useState(false)
    const [showConfirmation, setShowConfirmation] = useState(false)

    const handleShowBasket = (addedItem) => {
        setAddedItem(addedItem)
    }  

    const timer = () => {
        setTimeout(() => {
            setShowConfirmation(false)
        }, 3000);
    }

    const handleShowConfirmation = (addedItem) => {
        setShowConfirmation(addedItem)
        timer()
    }

    useEffect(() => {
        console.log("UNMOUNT")
    }, [])

    const newList = useMemo(() =>   
        <List 
            data = {location.state.value.refs}
            handleShowBasket = {handleShowBasket}
        ></List>
        , [location.state.value.refs]
    )

    console.log("Display Category Items")

        return (
            <>
            <div id="displayWrapper">
                <div id="displayCategoryItems">
                     <Title
                        css = "displayTitle"
                    >
                    - {location.state.categoryName} -
                    <h3>- {location.state.value.title} -</h3>
                    </Title>
                    {newList}
                    {addedItem && 
                        <DisplayProductBasket
                            data = {addedItem}
                            handleShowBasket = {handleShowBasket}
                            handleBasket = {props.handleBasket}
                            handleShowConfirmation = {handleShowConfirmation}
                            bagSize = {1}
                        >
                        </DisplayProductBasket>
                    }
                </div>
            </div>
            {showConfirmation && 
                    <table id = "showConfirmation" style = {{opacity: "1", background : "darkslategray", width: "100vw", bottom: "20vw", position : "fixed", color: "white", paddingTop: "20px", paddingBottom: "20px", margin: "auto", textAlign: "center"}}>
                        <tbody>
                            <tr>
                                <th>{showConfirmation.ref} - {showConfirmation.name} - {showConfirmation.bagSize * 100} g <br></br> Quantité : {showConfirmation.qty} <br></br>a bien été ajouté à votre panier<br></br><br></br>La Maison Mariage Frères vous remercie</th>
                            </tr>
                        </tbody>
                    </table>
            }
            </>
        )

};

export default DisplayCategoryItems;