import React, { useState } from 'react';
import { BrowserRouter as Router, Switch, Route, Link, useHistory } from 'react-router-dom';
import "./topmenu.css"
  
const Header = (props) => {

    console.log("header")

    const history = useHistory()

    const handleHistory = (address) => {
        history.push(`/${address}`)
    }

    return (
        <>
        <div>
            <div id = "headerWrapper">
                <div id = "header">
                    <Link to = "/accueil">
                        <div 
                            id = "centralLogo"
                        >
                        </div>
                    </Link>
                    <div id = "topMenu">
                        <table>
                            <tbody>
                               <tr onClick={() => handleHistory("account")}>
                                    <th
                                        className = "topMenuIcon"
                                        id = "topMenuLogin"
                                    ></th>
                                    <th
                                        className = "topMenuItem"
                                    >Mon Compte</th>
                               </tr>
                               <tr>
                                    <th
                                        className = "topMenuIcon"
                                        id = "topMenuFavs"
                                    ></th>
                                    <th
                                        className = "topMenuItem"
                                    >Mes Favoris</th>
                               </tr>
                               <tr onClick={() => handleHistory("basket")}>
                                    <th
                                        className = "topMenuIcon"
                                        id = "topMenuBasket"
                                    ></th>
                                    <th
                                        className = "topMenuItem"
                                    >Mon Panier</th>
                               </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
        </>
    );
}
  
export default Header;


